@font-face {
  font-style: normal;
  font-weight: 400;
  font-family: "";

  font-display: swap;
  src: url("../fonts/.woff2") format("woff2");
}

@font-face {
  font-style: normal;
  font-weight: 300;
  font-family: "roboto-condensed";
  font-display: swap;
  src:
    url("../fonts/roboto-condensed-light.woff2") format("woff2"),
    url("../fonts/roboto-condensed-light.woff") format("woff");
}


@font-face {
  font-style: normal;
