@font-face {
  font-style: normal;
  font-weight: 400;
  font-family: "";
  font-display: swap;
  src: url("../fonts/.woff2") format("woff2");
}
@font-face {
  font-style: normal;
  font-weight: 300;
  font-family: "roboto-condensed";
  font-display: swap;
  src: url("../fonts/roboto-condensed-light.woff2") format("woff2"), url("../fonts/roboto-condensed-light.woff") format("woff");
}
@font-face {
  font-style: normal;
  font-weight: 400;
  font-family: "roboto-condensed";
  font-display: swap;
  src: url("../fonts/roboto-condensed-regular.woff2") format("woff2"), url("../fonts/roboto-condensed-regular.woff") format("woff");
}
@font-face {
  font-style: normal;
  font-weight: 700;
  font-family: "roboto-condensed";
  font-display: swap;
  src: url("../fonts/roboto-condensed-bold.woff2") format("woff2"), url("../fonts/roboto-condensed-bold.woff") format("woff");
}
@font-face {
  font-style: normal;
  font-weight: 600;
  font-family: "Alumni Sans";
  font-display: swap;
  src: url("../fonts/AlumniSans-SemiBold.woff2") format("woff2"), url("../fonts/AlumniSans-SemiBold.woff") format("woff");
}
@font-face {
  font-style: normal;
  font-weight: 800;
  font-family: "Alumni Sans";
  font-display: swap;
  src: url("../fonts/AlumniSans-ExtraBold.woff2") format("woff2"), url("../fonts/AlumniSans-ExtraBold.woff") format("woff");
}
*,
*::before,
*::after {
  box-sizing: border-box;
}

html,
body {
  margin: 0;
  padding: 0;
  min-height: 100vh;
}

html {
  font-style: normal;
  font-weight: 400;
  font-size: 16px;
  line-height: 24px;
  font-family: "roboto-condensed", "Arial", sans-serif;
  color: #102654;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-rendering: optimizeLegibility;
}

body {
  width: 100%;
  height: 100%;
  background-color: #ffffff;
}

a {
  color: #000000;
  text-decoration: none;
}

img,
video {
  display: block;
  max-width: 100%;
  height: auto;
}

textarea {
  resize: none;
}

input:-webkit-autofill {
  box-shadow: inset 0 0 0 1000px #ffffff;
  -webkit-text-fill-color: #000000;
}

input,
textarea {
  border-radius: 0;
}
input::placeholder,
textarea::placeholder {
  opacity: 1;
}
input:invalid,
textarea:invalid {
  box-shadow: none;
}

select {
  border-radius: 0;
}

input::-ms-clear, input::-ms-reveal {
  display: none;
}

input[type=search]::-webkit-search-decoration,
input[type=search]::-webkit-search-cancel-button,
input[type=search]::-webkit-search-results-button,
input[type=search]::-webkit-search-results-decoration {
  appearance: none;
}

input::-webkit-outer-spin-button,
input::-webkit-inner-spin-button {
  margin: 0;
  appearance: none;
}

input[type=number] {
  appearance: textfield;
}

select,
textarea,
input:matches([type=email],
[type=number],
[type=password],
[type=search],
[type=tel],
[type=text],
[type=url]) {
  appearance: none;
}

button,
[type=button],
[type=reset],
[type=submit] {
  appearance: none;
}

.wrapper {
  position: relative;
  display: flex;
  flex-direction: column;
  min-height: 100vh;
  /* stylelint-disable-next-line */
  min-height: calc(100 * var(--vh, 1vh));
  overflow-x: hidden;
}
.wrapper .header,
.wrapper .footer {
  flex-shrink: 0;
}
.wrapper main {
  flex-grow: 1;
}

.rouble {
  font-family: "Rouble", "Arial", sans-serif;
  /* stylelint-disable */
  text-transform: lowercase !important;
  font-weight: 400 !important;
  /* stylelint-enable */
}

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  padding: 0;
  overflow: hidden;
  white-space: nowrap;
  border: 0;
  clip: rect(0 0 0 0);
  clip-path: inset(100%);
}

.scroll-lock-ios {
  position: fixed;
  overflow: hidden;
}

.scroll-lock {
  overflow: hidden;
}

.no-scrollbar {
  -ms-overflow-style: none;
  scrollbar-width: none;
}
.no-scrollbar::-webkit-scrollbar {
  display: none;
}

.no-transition {
  /* stylelint-disable-next-line */
  transition: none !important;
}

.js-focus-visible :focus:not(.focus-visible) {
  outline: none;
}

.container {
  width: 100%;
  max-width: 1240px;
  margin: 0 auto;
  padding: 0 40px;
}
@media (max-width: 1199px) {
  .container {
    padding: 0 45px;
  }
}
@media (max-width: 767px) {
  .container {
    padding: 0 15px;
  }
}

.btn {
  display: inline-flex;
  padding: 10px 20px;
  text-transform: uppercase;
  color: #ffffff;
  font-family: "roboto-condensed", "Arial", sans-serif;
  background-color: transparent;
  border: 4px solid #ffffff;
  cursor: pointer;
  font-weight: 700;
  font-size: 12px;
  line-height: 14px;
  letter-spacing: 0.05em;
  transition: opacity 0.3s ease;
  text-align: center;
  align-items: center;
  justify-content: center;
  box-sizing: border-box;
  user-select: none;
}
.btn--red {
  background-color: #ff1553;
}
.btn:disabled {
  opacity: 0.5;
  pointer-events: none;
}
@media (hover: hover) {
  .btn:hover:not(.focus-visible) {
    opacity: 0.8;
    border-color: #102654;
    outline: none;
  }
}
.btn.focus-visible:focus {
  opacity: 0.8;
  border-color: #102654;
  outline: none;
}

.header {
  padding-top: 30px;
  padding-bottom: 94px;
  color: #ffffff;
  background: conic-gradient(from 150.87deg at 16.03% 39.06%, #B80027 -46.69deg, #003D85 32.24deg, #B80027 313.31deg, #003D85 392.24deg);
}
@media (max-width: 1199px) {
  .header {
    background: conic-gradient(from 155.49deg at 20.7% 35.74%, #B80027 -46.69deg, #003D85 32.24deg, #B80027 313.31deg, #003D85 392.24deg);
    padding-top: 40px;
    padding-bottom: 105px;
  }
}
@media (max-width: 767px) {
  .header {
    background: conic-gradient(from 167.26deg at 35.31% 34.22%, #B80027 -46.69deg, #003D85 32.24deg, #B80027 313.31deg, #003D85 392.24deg);
    padding-top: 15px;
    padding-bottom: 78px;
  }
}

.header__navigation {
  position: relative;
  z-index: 1;
  display: flex;
  justify-content: space-between;
}

@media (max-width: 1199px) {
  .header__logo {
    width: 82px;
    height: 17px;
  }
}
@media (max-width: 767px) {
  .header__logo {
    width: 69px;
    height: 24px;
  }
}

.header__logo svg {
  display: block;
  width: 100%;
  height: 100%;
}

.header__tel-wrapper {
  display: flex;
  flex-direction: column;
}
@media (max-width: 1199px) {
  .header__tel-wrapper {
    margin-top: -6px;
  }
}
@media (max-width: 767px) {
  .header__tel-wrapper {
    margin-top: 0;
  }
}

.header__navigation a {
  font-family: "Alumni Sans", "Arial", sans-serif;
  color: #ffffff;
  text-decoration: none;
  font-size: 26px;
  line-height: 31px;
  font-weight: 600;
  letter-spacing: 0.03em;
}
@media (hover: hover) {
  .header__navigation a:hover:not(.focus-visible) {
    opacity: 0.8;
    color: #102654;
  }
}
.header__navigation a.focus-visible:focus {
  opacity: 0.8;
  color: #102654;
}
@media (max-width: 1199px) {
  .header__navigation a {
    font-size: 24px;
    line-height: 29px;
    letter-spacing: 0.02em;
  }
}
@media (max-width: 767px) {
  .header__navigation a {
    font-weight: 600;
    font-size: 20px;
    line-height: 24px;
  }
}

.promo__content {
  display: grid;
  gap: 15px 17px;
  grid-template-columns: 1fr 1fr;
  margin-bottom: 49px;
}
@media (max-width: 1199px) {
  .promo__content {
    gap: 8px 25px;
    margin-bottom: 37px;
  }
}
@media (max-width: 767px) {
  .promo__content {
    gap: 15px 0;
    margin-bottom: 23px;
    grid-template-columns: minmax(100px, 200px) 1fr;
  }
}

.promo__wrapper {
  position: relative;
  display: flex;
  flex-direction: column;
  max-width: 396px;
  padding-top: 88px;
}
@media (max-width: 1199px) {
  .promo__wrapper {
    max-width: 50%;
    padding-top: 68px;
    margin-left: -15px;
  }
}
@media (max-width: 767px) {
  .promo__wrapper {
    max-width: 100%;
    width: 100%;
    margin-left: 0;
  }
}
.promo__wrapper::before {
  content: "";
  position: absolute;
  width: 500px;
  height: 556px;
  background-image: url("../img/header-man.jpg");
  background-repeat: no-repeat;
  background-size: cover;
  left: 460px;
  top: -23px;
}
@media (min-width: 1200px) and (min-resolution: 144dpi), (min-width: 1200px) and (min-resolution: 1.5dppx) {
  .promo__wrapper::before {
    background-image: url("../img/header-man@2x.jpg");
  }
}
@media (max-width: 1199px) {
  .promo__wrapper::before {
    width: 324px;
    height: 390px;
    background-image: url("../img/header-man-tablet.jpg");
    top: 19px;
    left: 109%;
  }
}
@media (max-width: 1199px) and (min-resolution: 144dpi), (max-width: 1199px) and (min-resolution: 1.5dppx) {
  .promo__wrapper::before {
    background-image: url("../img/header-man-tablet@2x.jpg");
  }
}
@media (max-width: 767px) {
  .promo__wrapper::before {
    display: none;
  }
}

.promo h1 {
  margin: 0;
  padding: 0;
  font-family: "Alumni Sans", "Arial", sans-serif;
  grid-row: 2/3;
  grid-column: 2/3;
  font-weight: 600;
  font-size: 92px;
  line-height: 110px;
  letter-spacing: 0.05em;
  text-align: right;
  text-transform: uppercase;
  -webkit-text-stroke: 4px;
}
@media (max-width: 1199px) {
  .promo h1 {
    font-size: 72px;
    line-height: 86px;
    text-transform: none;
  }
}
@media (max-width: 767px) {
  .promo h1 {
    max-width: 300px;
    margin-left: auto;
  }
}

.promo span {
  margin: 0;
  padding: 0;
  font-family: "Alumni Sans", "Arial", sans-serif;
  grid-row: 1/2;
  grid-column: 2/3;
  font-weight: 600;
  font-size: 48px;
  line-height: 58px;
  text-align: right;
  margin-right: 51px;
  letter-spacing: 0.03em;
}
@media (max-width: 1199px) {
  .promo span {
    font-size: 34px;
    line-height: 41px;
    margin-right: 4px;
  }
}
@media (max-width: 767px) {
  .promo span {
    font-size: 28px;
    line-height: 34px;
  }
}

.promo__list {
  margin: 0;
  padding: 0;
  list-style-type: none;
  grid-row: 2/3;
  grid-column: 1/2;
  text-align: right;
  align-self: flex-end;
  padding-bottom: 36px;
}
@media (max-width: 1199px) {
  .promo__list {
    padding-bottom: 20px;
  }
}
@media (max-width: 767px) {
  .promo__list {
    margin-right: auto;
    width: 113px;
    padding-bottom: 32px;
  }
}

.promo__list li {
  font-weight: 400;
  font-size: 14px;
  line-height: 21px;
}

.promo__button {
  align-self: flex-end;
  padding: 19px 55px;
}
@media (max-width: 1199px) {
  .promo__button {
    padding-left: 31px;
    padding-right: 31px;
  }
}
@media (max-width: 767px) {
  .promo__button {
    width: 100%;
    padding-top: 20px;
    padding-bottom: 20px;
  }
}

.about__container {
  padding-top: 120px;
  padding-bottom: 7px;
  display: grid;
  grid-template-columns: minmax(300px, 355px) 1fr;
  gap: 85px;
  color: #102654;
  padding-left: 60px;
}
@media (max-width: 1199px) {
  .about__container {
    display: flex;
    flex-direction: column;
    align-items: center;
    padding-top: 100px;
    padding-bottom: 16px;
    gap: 43px;
    padding-left: 0;
  }
}
@media (max-width: 767px) {
  .about__container {
    padding-top: 64px;
    padding-bottom: 110px;
    gap: 33px;
  }
}

@media (max-width: 767px) {
  .about .container {
    padding: 0;
  }
}

.about__description {
  padding-top: 40px;
}
@media (max-width: 1199px) {
  .about__description {
    padding-top: 0;
    text-align: center;
  }
}
@media (max-width: 767px) {
  .about__description {
    padding: 0 15px;
    text-align: left;
  }
}

.about h2 {
  font-family: "Alumni Sans", "Arial", sans-serif;
  font-weight: 600;
  font-size: 48px;
  line-height: 58px;
  margin: 0;
  margin-bottom: 5px;
  letter-spacing: 0.03em;
}
@media (max-width: 1199px) {
  .about h2 {
    margin-bottom: -1px;
    margin-left: -16px;
    font-size: 42px;
    line-height: 50px;
  }
}
@media (max-width: 767px) {
  .about h2 {
    font-size: 32px;
    line-height: 38px;
    margin-left: 0;
    margin-bottom: 4px;
  }
}

.about h3 {
  margin: 0;
  font-weight: 400;
  font-size: 18px;
  line-height: 27px;
  margin-bottom: 40px;
}
@media (max-width: 1199px) {
  .about h3 {
    margin-bottom: 35px;
  }
}
@media (max-width: 767px) {
  .about h3 {
    margin-bottom: 25px;
  }
}

.about p {
  margin: 0;
  font-weight: 300;
  font-size: 16px;
  line-height: 24px;
  margin-bottom: 8px;
}
@media (max-width: 1199px) {
  .about p {
    max-width: 510px;
    margin: auto;
  }
}
.about p:last-child {
  margin-bottom: 0;
}

@media (max-width: 1199px) {
  .about__description p:last-of-type {
    display: none;
  }
}

.about__media {
  width: 100%;
}

.about__picture img {
  background-color: #f8f9fc;
  width: 100%;
  height: 100%;
}
@media (max-width: 1199px) {
  .about__picture img {
    aspect-ratio: 678/345;
  }
}
@media (max-width: 767px) {
  .about__picture img {
    object-fit: cover;
    aspect-ratio: 320/313;
  }
}

.about__video {
  margin: 0;
  position: relative;
  width: 55%;
  min-height: 228px;
  background-color: #102654;
  transform: translate(-17%, -50%);
}
@media (max-width: 1199px) {
  .about__video {
    min-height: 167px;
    width: 39%;
    transform: translate(78%, -50%);
  }
}
@media (max-width: 767px) {
  .about__video {
    transform: translate(0, 40px);
    width: 100%;
    aspect-ratio: 2/1;
  }
}

.about__button {
  position: absolute;
  top: 50%;
  left: 50%;
  width: 100px;
  height: 100px;
  transform: translate(-50%, -50%);
  border: none;
  background: transparent;
  cursor: pointer;
  padding: 0;
}
@media (max-width: 1199px) {
  .about__button {
    width: 73px;
    height: 73px;
  }
}
@media (hover: hover) {
  .about__button:hover:not(.focus-visible) {
    opacity: 0.8;
